function createElement(tag, attributes = {}, children = []) {
  return { tag, attributes, children };
}

function template(state, props) {
  return createElement("div", {}, [
    createElement("h1", {}, [state.count]),
    createElement("button", { onClick: handleClick }, ["Add"]),
  ]);
}

let state = { count: 0 };

function updateState(newState) {
  state = { ...state, ...newState };
  patch(view, render(state));
  console.log("State updated:", state);
}

function handleClick() {
  updateState({ count: state.count + 1 });
}

let view;

function componentDidMount() {
  console.log("Component mounted");
}

function render(state) {
  const newView = patch(view, template(state));
  if (!view) {
    componentDidMount();
  }
  view = newView;
  return view;
}

export function startApp(container) {
  patch(container, render(state));
}
